import React from 'react';
import Delay from 'react-delay';

export const Home = () => {
  return (
    <div className="home-container">
      <div className="home-title">
        <Delay wait={100}>
          <h1>
            Jakub<br />Biesiada
          </h1>
        </Delay>
        <h2>Front-End Developer</h2>
      </div>
    </div>
  );
};



