import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import Menu from './components/Menu/';
import { Content } from './components/Content';
import './App.css';

export default class App extends Component {
  state = {
    x: 0,
    y: 0,
    height: null,
    offset: false
  };

  componentDidMount() {
    this.setHeight();

    document.addEventListener('touchstart', e => e.preventDefault());
    window.addEventListener('resize', this.setHeight);
    window.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener('devicemotion', this.onDeviceMove);
  }

  setOffset = offset => this.setState({ offset: offset });
  onTouch = e => e.preventDefault();
  setHeight = () => this.setState({ height: window.innerHeight });

  onDeviceMove = e => {
    this.setState({
      x: e.accelerationIncludingGravity.x * 2,
      y: e.accelerationIncludingGravity.y * 2
    });
  };

  onMouseMove = e => {
    this.setState({
      x: (e.clientX - window.innerWidth / 2) / 40,
      y: (e.clientY - window.innerHeight / 2) / 40
    });
  };

  render() {
    return (
      <Router basename={'/portfolio-alpha'}>
        <React.Fragment>
          <div
            className="back"
            style={{
              height: this.state.height,
              transform: `scale(1.09) translate(${this.state.x}px, ${
                this.state.y
              }px)`
            }}
          />

          <Menu offset={this.setOffset} height={this.state.height} />

          <div
            className={this.state.offset ? 'container zoom' : 'container'}
            style={{ height: this.state.height - 76 }}>
            <div className="content">
              <Content />
            </div>
          </div>
        </React.Fragment>
      </Router>
    );
  }
}



