import React from 'react';
import Delay from 'react-delay';
//import { Link } from 'react-router-dom';

//import spi11NewsFeed from '../images/portfolio/spi11/newsfeed.png';
//import tilt from '../images/portfolio/tilt/tilt.png';

export const Portfolio = () => {
  return (
    <React.Fragment>
      <Delay wait={100}>
        <h2>Projekty</h2>
      </Delay>
      <div className="portfolio">
        <Delay wait={250}>
          <div onClick={this.open} className="scale">
            Już wkrótce!
          </div>
        </Delay>
        <Delay wait={400}>
          <div>Na horyzoncie!</div>
        </Delay>
        <Delay wait={550}>
          <div>Niebawem!</div>
        </Delay>
        <Delay wait={700}>
          <div>Zaraz wracam!</div>
        </Delay>
        <Delay wait={850}>
          <div>Tuż tuż!</div>
        </Delay>
      </div>
    </React.Fragment>
  );
};



