import React from 'react';
import Delay from 'react-delay';

import html5 from '../images/technologies/html5.png';
import css from '../images/technologies/css.png';
import js from '../images/technologies/js.png';
import rwd from '../images/technologies/rwd.png';
import bootstrap from '../images/technologies/bootstrap.png';
import materialize from '../images/technologies/materialize.png';
import sass from '../images/technologies/sass.png';
import react from '../images/technologies/react.png';
import electron from '../images/technologies/electron.png';
import jQuery from '../images/technologies/jQuery.png';
import wordpress from '../images/technologies/wordpress.png';
import github from '../images/technologies/github.png';
import gulp from '../images/technologies/gulp.png';
import npm from '../images/technologies/npm.png';
import atom from '../images/technologies/atom.png';
import webpack from '../images/technologies/webpack.png';
import macos from '../images/technologies/macos.png';
import windows from '../images/technologies/windows.png';
import linux from '../images/technologies/linux.png';

export const Technologies = () => {
  return (
    <React.Fragment>
      <Delay wait={100}>
        <h2>Technologie</h2>
      </Delay>
      <div className="tech">
        <Delay wait={200}>
          <div>
            <img src={html5} alt="html5" />
          </div>
        </Delay>
        <Delay wait={300}>
          <div>
            <img src={css} alt="css" />
          </div>
        </Delay>
        <Delay wait={400}>
          <div>
            <img src={js} alt="js" />
          </div>
        </Delay>
        <Delay wait={500}>
          <div>
            <img src={rwd} alt="rwd" />
          </div>
        </Delay>
        <Delay wait={600}>
          <div>
            <img src={bootstrap} alt="bootstrap" />
          </div>
        </Delay>
        <Delay wait={700}>
          <div>
            <img src={materialize} alt="materialize" />
          </div>
        </Delay>
        <Delay wait={800}>
          <div>
            <img src={sass} alt="sass" />
          </div>
        </Delay>
        <Delay wait={900}>
          <div>
            <img src={react} alt="react" />
          </div>
        </Delay>
        <Delay wait={1000}>
          <div>
            <img src={electron} alt="electron" />
          </div>
        </Delay>
        <Delay wait={1100}>
          <div>
            <img src={jQuery} alt="jQuery" />
          </div>
        </Delay>
        <Delay wait={1200}>
          <div>
            <img src={wordpress} alt="wordpress" />
          </div>
        </Delay>
        <Delay wait={1300}>
          <div>
            <img src={github} alt="github" />
          </div>
        </Delay>
        <Delay wait={1400}>
          <div>
            <img src={gulp} alt="gulp" />
          </div>
        </Delay>
        <Delay wait={1500}>
          <div>
            <img src={webpack} alt="webpack" />
          </div>
        </Delay>
        <Delay wait={1600}>
          <div>
            <img src={npm} alt="npm" />
          </div>
        </Delay>
        <Delay wait={1700}>
          <div>
            <img src={atom} alt="atom" />
          </div>
        </Delay>
        <Delay wait={1800}>
          <div>
            <img src={macos} alt="macos" />
          </div>
        </Delay>
        <Delay wait={1900}>
          <div>
            <img src={windows} alt="windows" />
          </div>
        </Delay>
        <Delay wait={2000}>
          <div>
            <img src={linux} alt="linux" />
          </div>
        </Delay>
      </div>
    </React.Fragment>
  );
};



