import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import Delay from 'react-delay';

export default class Menu extends Component {
  state = {
    isOpen: false,
    isMobile: null
  };

  componentDidMount() {
    this.onResize();
    window.addEventListener('resize', () => this.onResize());
  }

  onResize() {
    if (this.refs.nav.offsetHeight > 74) {
      this.setState({ isMobile: true });
      if (this.state.isOpen) {
        this.isOffset(true);
      }
    } else {
      this.setState({ isMobile: false });
      this.isOffset(false);
    }
  }

  isOffset = value => this.props.offset(value);

  toggleMenu = () => {
    let isOpen;

    if (this.state.isOpen === false) isOpen = true;
    else if (this.state.isOpen === true) isOpen = false;

    if (this.state.isMobile) {
      this.setState({ isOpen: isOpen });
      this.isOffset(isOpen);
    }
  };

  render() {
    return (
      <nav>
        <div>
          <ul className="icons">
            <Delay wait={100}>
              <a
                href="https://github.com/JB1905"
                target="_blank"
                rel="noopener noreferrer"
                className="github">
                <div />
              </a>
            </Delay>
            <Delay wait={250}>
              <a
                href="https://www.linkedin.com/in/jakub-biesiada"
                target="_blank"
                rel="noopener noreferrer"
                className="linkedin">
                <div />
              </a>
            </Delay>
            <Delay wait={400}>
              <a
                href="https://www.facebook.com/kuba.biesiada"
                target="_blank"
                rel="noopener noreferrer"
                className="fb">
                <div />
              </a>
            </Delay>
            <Delay wait={550}>
              <a
                href="https://twitter.com/jbiesiad"
                target="_blank"
                rel="noopener noreferrer"
                className="twitter">
                <div />
              </a>
            </Delay>
          </ul>

          <div className="nav" ref="nav">
            <ul
              className={`desktop ${this.state.isMobile ? 'hidden' : ''}`}
              ref="list">
              <li>
                <NavLink exact to="/">
                  Start
                </NavLink>
              </li>
              <li>
                <NavLink to="/about">O mnie</NavLink>
              </li>
              <li>
                <NavLink to="/technologies">Technologie</NavLink>
              </li>
              <li>
                <NavLink to="/portfolio">Projekty</NavLink>
              </li>
              <li>
                <NavLink to="/contact">Kontakt</NavLink>
              </li>
            </ul>
            {this.state.isMobile ? (
              <div
                className={`mobile ${this.state.isOpen ? 'opened' : 'closed'}`}>
                <Delay wait={700}>
                  <button className="menu" onClick={this.toggleMenu}>
                    <div>
                      <div />
                      <div />
                      <div />
                    </div>
                  </button>
                </Delay>
                {this.state.isOpen ? (
                  <ul
                    ref="list"
                    className={`list ${
                      this.state.isOpen ? 'opened' : 'closed'
                    }`}
                    style={{ height: this.props.height - 76 }}>
                    <Delay wait={250}>
                      <div>
                        <li style={{ animationDuration: '250ms' }}>
                          <NavLink exact to="/" onClick={this.toggleMenu}>
                            Start
                          </NavLink>
                        </li>
                        <li style={{ animationDuration: '400ms' }}>
                          <NavLink to="/about" onClick={this.toggleMenu}>
                            O mnie
                          </NavLink>
                        </li>
                        <li style={{ animationDuration: '550ms' }}>
                          <NavLink to="/technologies" onClick={this.toggleMenu}>
                            Technologie
                          </NavLink>
                        </li>
                        <li style={{ animationDuration: '700ms' }}>
                          <NavLink to="/portfolio" onClick={this.toggleMenu}>
                            Projekty
                          </NavLink>
                        </li>
                        <li style={{ animationDuration: '850ms' }}>
                          <NavLink to="/contact" onClick={this.toggleMenu}>
                            Kontakt
                          </NavLink>
                        </li>
                      </div>
                    </Delay>
                  </ul>
                ) : null}
              </div>
            ) : null}
          </div>
        </div>
      </nav>
    );
  }
}



